/* RESET */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', sans-serif;
  background-color: #f9f9f9;
  color: #333;
}

/* Layout */
.app-container {
  display: flex;
  height: 100vh;
}

/* Sidebar */
.sidebar {
  width: 240px;
  background-color: #ffffff;
  border-right: 1px solid #ddd;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.sidebar-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 30px;
}

.logo {
  width: 36px;
  height: 36px;
}

.company-name {
  font-size: 1.25rem;
  font-weight: bold;
}

/* Nav links */
.nav-links {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.nav-link {
  text-decoration: none;
  color: #555;
  font-weight: 500;
  transition: all 0.2s ease;
}

.nav-link:hover {
  color: #000;
}

.nav-link.active {
  color: #000;
  font-weight: 700;
}

/* Content */
.main-content {
  flex-grow: 1;
  padding: 40px;
  overflow-y: auto;
}

.login-container {
  max-width: 400px;
  margin: 80px auto;
  padding: 2rem;
  border-radius: 10px;
  background-color: #f8f8f8;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  text-align: center;
}

.loginInput {
  display: block;
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 1rem;
  font-size: 1rem;
}

#loginBtn {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  background-color: black;
  color: white;
  border: none;
  cursor: pointer;
}

.error {
  color: red;
  margin-top: 1rem;
}

.stock-page {
  max-width: 800px;
  margin: 2rem auto;
  padding: 0 1rem;
  font-family: Arial, sans-serif;
}

.new-item-form {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}

.new-item-form input {
  flex: 1 1 150px;
  padding: 0.5rem;
  font-size: 1rem;
}

.new-item-form button {
  padding: 0.5rem 1rem;
  background-color: black;
  color: white;
  border: none;
  cursor: pointer;
}

.items-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.item-row {
  display: grid;
  grid-template-columns: 3fr 1fr 1fr auto auto;
  gap: 1rem;
  padding: 0.75rem 0;
  border-bottom: 1px solid #ccc;
  align-items: center;
}

.item-row input {
  padding: 0.25rem;
  font-size: 1rem;
  width: 100%;
}

.item-row button {
  background: none;
  border: 1px solid black;
  padding: 0.25rem 0.75rem;
  cursor: pointer;
  font-size: 0.9rem;
}

.item-row button:hover {
  background-color: black;
  color: white;
}
